import React from 'react';
import {CrossIcon} from "../Icons";
import './map.less';

class Map extends React.Component {

    constructor(props) {
        super(props);
    }

    onCrossClick() {
        this.props.close();
    }

    render() {
        return <div className="map_container">
            <CrossIcon className='cross' onClick={() => this.onCrossClick()}/>
        </div>
    }
}

export default Map;



